import React from 'react'
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import { dict } from '../../Dict';
import Header from "../header/header.jsx";
var t = dict['farsi']

export default class ProfileIndex extends React.Component {

    constructor(props) {
        super(props);
        //this.submit = this.submit.bind(this);
        this.getList = this.getList.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.state = {
            token: window.localStorage.getItem('token'),
            lang: window.localStorage.getItem('lang'),
            dir: window.localStorage.getItem('dir'),
            profiles: [],
            page: 1,
            pages: 1,
            q: '',
        }

    }



    componentWillMount() {
        ModelStore.on("got_list", this.getList);

    }

    componentWillUnmount() {
        ModelStore.removeListener("got_list", this.getList);
    }

    componentDidMount() {
        t = dict[this.state.lang]
        MyActions.getList('profiles/search', this.state.page, { page: this.state.page }, this.state.token)
    }



    handleChange(obj) {

    }

    getList() {
        var list = ModelStore.getList()
        var klass = ModelStore.getKlass()
        if (list && klass === 'Profile') {
            if (list[0] && list[0].page) {
                if (list[0].page == 1) {
                    this.setState({
                        profiles: list,
                    });
                } else {
                    this.setState({
                        profiles: this.state.profiles.concat(list),
                    });
                }
                this.setState({ page: list[0].page, pages: list[0].pages })
            } else {
                this.setState({
                    profiles: [],
                    pages: 0,
                });
            }
        }
        console.log(list)
    }

    tags(profile) {
        var result = []
        profile.tags.map((tag) => {
            result.push(<span class="badge bg-lime-lt" style={{ margin: '2px' }}>{tag.title}</span>)
        })
        return result
    }


    avatar(profile) {
        if (profile.cover) {
            return (
                <span class="avatar avatar-xl mb-3 avatar-rounded" style={{ backgroundImage: "url(" + profile.cover + ")" }} ></span>
            )
        } else {
            return (
                <span class="avatar avatar-xl mb-3 avatar-rounded" >
                    {profile.initials}
                </span>
            )
        }
    }

    items() {
        var result = []
        this.state.profiles.map((profile) => {
            result.push(
                <div class="col-md-6 col-lg-3">
                    <div class="card">
                        <div class="card-body p-4 text-center">
                        {this.avatar(profile)}
                            <h3 class="m-0 mb-1"><a href={"#/profiles/"+profile.id}>{profile.name}</a></h3>
                            <div class="text-muted">{profile.short_bio}</div>
                            <div class="mt-3">
                                <div className='example-content'>
                                    {this.tags(profile)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        })
        return result
    }

    search(q) {
        if (q.length > 3) {
            this.setState({ q: q, page: 1 }, () =>
                MyActions.getList('profiles/search', this.state.page, { q: q }, this.state.token)
            );
        } else {
            this.setState({ q: '', page: 1 }, () =>
                MyActions.getList('profiles/search', this.state.page, {}, this.state.token)
            );
        }
    }


    loadMore() {
        var data = { page: this.state.page + 1, q: this.state.q }
        MyActions.getList('profiles/search', this.state.page + 1, data, this.state.token);
    }

    moreBtn() {
        if (this.state.pages > this.state.page) {
            return (
                <div class="hr-text">
                    <a onClick={() => this.loadMore()}>
                        <svg xmlns="http://www.w3.org/2000/svg" class="" width="16" height="16" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><circle cx="12" cy="12" r="9" /><line x1="8" y1="12" x2="12" y2="16" /><line x1="12" y1="8" x2="12" y2="16" /><line x1="16" y1="12" x2="12" y2="16" /></svg>
                        {t['more']}
                    </a>
                </div>
            )
        }
    }


    render() {

        return (
            <body className="antialiased">
                <div className="wrapper">
                    <Header history={this.props.history} />
                    <div className="page-wrapper">
                        <div className="container-xl">
                            <div className="page-header d-print-none">
                                <div className="row align-items-center"></div>
                                <div className="col">
                                    <div className="page-pretitle">{t['overview']}</div>
                                    <h2 className="page-title">{t['profiles']}</h2>
                                </div>
                            </div>
                        </div>
                        <div className="page-body">
                            <div className="container-xl">
                                <div className="row row-deck row-cards">
                                    <div class="mb-3">
                                        <div class="input-icon mb-3">
                                            <input type="text" class="form-control form-giant" placeholder={t['search_placeholder']} onInput={(e) => this.search(e.target.value)} />
                                            <span class="input-icon-addon">

                                                <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><circle cx="10" cy="10" r="7"></circle><line x1="21" y1="21" x2="15" y2="15"></line></svg>
                                            </span>
                                        </div>
                                    </div>
                                    {this.items()}
                                    {this.moreBtn()}

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </body>
        )
    }
}




