import React from 'react'
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import $ from 'jquery';
import { dict } from '../../Dict';
import { conf } from '../../conf';
import Validation from "../common/validation.jsx";
import { CountryDropdown, RegionDropdown, CountryRegionData } from 'react-country-region-selector';
import { Typeahead, withAsync } from 'react-bootstrap-typeahead';
import {
    validateExistence
} from "../common/validate.js";

const AsyncTypeahead = withAsync(Typeahead);
const server = conf.server;
var t = dict['farsi']




export default class UserWizard extends React.Component {

    constructor(props) {
        super(props);
        this.submit = this.submit.bind(this);
        this.setInstance = this.setInstance.bind(this);
        this.getInstance = this.getInstance.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.modal = React.createRef();
        this.validateExistence = validateExistence.bind(this);


        this.state = {
            token: window.localStorage.getItem('token'),
            lang: window.localStorage.getItem('lang'),
            dir: window.localStorage.getItem('dir'),
            email: null,
            name: null,
            password: null,
            password_confirmation: null,
            id: null,
            bio: null,
            country: null,
            initials: 'NN',
            options: [],
            tags: [],
            validationItems: [],
            disableSubmit: true,
            twitter: null,
            instagram: null,
            telegram: null,
            email: null,
            phone: null,
            linkdin: null,
        }

    }



    componentWillMount() {
        ModelStore.on("set_instance", this.setInstance);
        ModelStore.on("got_instance", this.getInstance);
    }

    componentWillUnmount() {
        ModelStore.removeListener("set_instance", this.setInstance);
        ModelStore.removeListener("got_instance", this.getInstance);
    }

    componentDidMount() {
        t = dict[this.state.lang]
        if (this.state.token && this.state.token !== '') {
            MyActions.getInstance('profiles/my', '1', this.state.token);
        }
    }

    getInstance() {
        var profile = ModelStore.getIntance()
        var klass = ModelStore.getKlass()
        if (profile && klass === 'Profile') {
            this.setState({
                id: profile.id,
                name: profile.name,
                bio: profile.bio,
                country: profile.country,
                initials: profile.initials,
                tags: profile.tags,
                twitter: profile.twitter,
                instagram: profile.instagram,
                telegram: profile.telegram,
                email: profile.email,
                phone: profile.phone,
                linkdin: profile.linkdin,
            })
            console.log(profile.tags)
        }
    }

    validateEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }


    handleChange(obj) {
        this.setState(obj);
        if (Object.keys(obj) && Object.keys(obj)[0] && Object.keys(obj)[0] === 'password_confirmation') {
            if (obj['password_confirmation'] !== this.state.password) {
                this.setState({ disableSubmit: true });
                $('#password_confirmation').addClass(' is-invalid ')
            } else {
                this.setState({ disableSubmit: false });
                $('#password_confirmation').removeClass(' is-invalid ').addClass(' is-valid ')
            }
        }

        if (Object.keys(obj) && Object.keys(obj)[0] && Object.keys(obj)[0] === 'password') {
            if (obj['password'].length < 8) {
                this.setState({ disableSubmit: true });
                $('#password').addClass(' is-invalid ')
            } else {
                this.setState({ disableSubmit: false });
                $('#password').removeClass(' is-invalid ').addClass(' is-valid ')
            }
        }

        if (Object.keys(obj) && Object.keys(obj)[0] && Object.keys(obj)[0] === 'email') {
            if (!this.validateEmail(obj['email'])) {
                this.setState({ disableSubmit: true });
                $('#email').addClass(' is-invalid ')
            } else {
                this.setState({ disableSubmit: false });
                $('#email').removeClass(' is-invalid ').addClass(' is-valid ')
            }
        }
    }

    setInstance() {
        var klass = ModelStore.getKlass()
        var model = ModelStore.getIntance()
        if (klass === 'SignUp') {
            window.localStorage.setItem('token', model.token);
            window.location.reload()
        }
        if (klass === 'Profile') {
            this.props.history.push("/#/")
        }
    }


    submit() {
        if (this.validateExistence(['email', 'name', 'password', 'password_confirmation'])) {
            $('#submit-button').hide();
            $('#submit-spinner').show();
            var data = this.state
            MyActions.setInstance('users/sign_up', data);
        }
    }

    submitProfile() {
        var data = this.state
        MyActions.updateInstance('profiles', data, this.state.token);
    }

    tagSelected(tags) {
        var result = []
        tags.map((tag) => {
            if (tag['title']) {
                result.push(tag['title'])
            } else {
                result.push(tag)
            }
        })
        this.setState({ tags: result }, () => console.log(this.state.tags))
    }

    checkSignUp() {
        if (!this.state.token || this.state.token === '') {
            return (this.notSignedUp())
        } else {
            return (this.signedUp())
        }
    }

    notSignedUp() {
        return (
            <React.Fragment>
                <div class="text-center mb-4">
                    <a href="."><img src="./static/logo.svg" height="36" alt="" /></a>
                </div>
                <div class="card card-md">
                    <div class="card-body text-center py-4 p-sm-5">
                        <img src="./static/illustrations/undraw_sign_in_e6hj.svg" height="128" class="mb-n2" alt="" />
                        <h1 class="mt-5">{t['welcome']}</h1>
                        <p class="text-muted">{t['welcome_note']}</p>
                        <p class="text-muted">{t['login_if_registered']}</p>
                        <a href="/#/login" class="btn btn-outline-success w-100">
                            {t['login']}
                        </a>
                    </div>
                    <div class="hr-text hr-text-center hr-text-spaceless">{t['sign_up_now']}</div>
                    <div class="card-body">
                        <div class="mb-3">
                            <label class="form-label">{t['your_email']}</label>
                            <input type="text" id='email' name='email' onInput={(e) => { this.handleChange({ email: e.target.value }) }} class="form-control ps-1" autocomplete="off" />
                            <div class="form-hint"></div>
                        </div>

                        <div class="mb-3">
                            <label class="form-label">{t['your_name']}</label>
                            <div class="input-group input-group-flat">
                                <span class="input-group-text"></span>
                                <input type="text" name='fullname' onInput={(e) => { this.handleChange({ name: e.target.value }) }} class="form-control ps-1" autocomplete="off" />
                            </div>
                            <div class="form-hint"></div>
                        </div>

                        <div class="mb-3">
                            <label class="form-label">{t['choose_password']}</label>
                            <input type="password" id='password' onInput={(e) => { this.handleChange({ password: e.target.value }) }} class="form-control ps-1 is-valid" autocomplete="off" />
                            <div class="form-hint">{t['password_rule']}</div>
                        </div>

                        <div class="mb-3">
                            <label class="form-label">{t['repeat_password']}</label>

                            <input type="password" id='password_confirmation' onInput={(e) => { this.handleChange({ password_confirmation: e.target.value }) }} class="form-control ps-1" autocomplete="off" />
                            <div class="form-hint"></div>
                        </div>

                    </div>
                </div>
                <div class="row align-items-center mt-3">
                    <div class="col-4">
                        <div class="progress">
                            <div class="progress-bar" style={{ width: "25%" }} role="progressbar" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                <span class="visually-hidden">25% Complete</span>
                            </div>
                        </div>
                    </div>
                    <div class="col">
                        <div class="btn-list justify-content-end">
                            <button id='submit-button' onClick={() => this.submit()} disabled={this.state.disableSubmit} class="btn btn-primary">
                                {t['sign_up_and_continue']}
                            </button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }

    signedUp() {
        if (this.state.id) {
            return (
                <React.Fragment>
                    <div class="card card-md">
                        <div class="card-body text-center py-4 p-sm-5">
                            <span class="avatar avatar-xl mb-2 avatar-rounded">{this.state.initials}</span>
                            <p class="text-muted"></p>
                        </div>

                        <div class="hr-text hr-text-center hr-text-spaceless">{t['complete_your_profile']}</div>

                        <div class="card-body">
                            <div class="mb-3">
                                <label class="form-label">{t['your_country']}</label>
                                <div class="input-group input-group-flat">
                                    <span class="input-group-text"></span>
                                    <CountryDropdown classes={"form-control ps-1"} priorityOptions={['CA', 'IR', 'US']} value={this.state.country} onChange={(val) => this.handleChange({ country: val })} />
                                </div>
                            </div>

                            <div class="mb-3">
                                <label class="form-label">{t['bio']}<span class="form-label-description"></span></label>
                                <textarea
                                    class="form-control"
                                    name="example-textarea-input"
                                    rows="6"
                                    placeholder={t['write_about_yourself']}
                                    onInput={(e) => { this.handleChange({ bio: e.target.value }) }}
                                    value={this.state.bio}>
                                </textarea>
                            </div>
                            <div class="mb-3">
                                <label class="form-label" >{t['tags']}</label>
                                <AsyncTypeahead
                                    id='yytyty'
                                    allowNew
                                    multiple
                                    defaultSelected={this.state.tags}
                                    isLoading={this.state.isLoading}
                                    labelKey='title'
                                    onSearch={(query) => {
                                        this.setState({ isLoading: true });
                                        fetch(server + "/tags/search?q=" + query)
                                            .then((resp) => resp.json())
                                            .then(({ items }) => {
                                                this.setState({ options: items, isLoading: false }, () => console.log(this.state.options))
                                            });
                                    }}
                                    onChange={(t) => this.tagSelected(t)}
                                    options={this.state.options}
                                    renderMenuItemChildren={(option, props) => (
                                        <React.Fragment>
                                            <span>{option.title}</span>
                                        </React.Fragment>
                                    )}
                                />
                            </div>
                            <div class="mb-3" >
                                <div class="input-group input-group-flat">
                                    <input type="text" dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ email: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.email} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><rect x="3" y="5" width="18" height="14" rx="2" /><polyline points="3 7 12 13 21 7" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>
                            <div class="mb-3">
                                <div class="input-group input-group-flat">
                                    <input type="text"  dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ phone: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.phone} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M5 4h4l2 5l-2.5 1.5a11 11 0 0 0 5 5l1.5 -2.5l5 2v4a2 2 0 0 1 -2 2a16 16 0 0 1 -15 -15a2 2 0 0 1 2 -2" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>
                            <div class="mb-3">
                                <div class="input-group input-group-flat">
                                    <input type="text"  dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ linkdin: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.linkdin} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><rect x="4" y="4" width="16" height="16" rx="2" /><line x1="8" y1="11" x2="8" y2="16" /><line x1="8" y1="8" x2="8" y2="8.01" /><line x1="12" y1="16" x2="12" y2="11" /><path d="M16 16v-3a2 2 0 0 0 -4 0" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>
                            <div class="mb-3">
                                <div class="input-group input-group-flat">
                                    <input type="text"  dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ twitter: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.twitter} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M22 4.01c-1 .49 -1.98 .689 -3 .99c-1.121 -1.265 -2.783 -1.335 -4.38 -.737s-2.643 2.06 -2.62 3.737v1c-3.245 .083 -6.135 -1.395 -8 -4c0 0 -4.182 7.433 4 11c-1.872 1.247 -3.739 2.088 -6 2c3.308 1.803 6.913 2.423 10.034 1.517c3.58 -1.04 6.522 -3.723 7.651 -7.742a13.84 13.84 0 0 0 .497 -3.753c-.002 -.249 1.51 -2.772 1.818 -4.013z" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>
                            <div class="mb-3">
                                <div class="input-group input-group-flat">
                                    <input type="text"  dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ telegram: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.telegram} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M15 10l-4 4l6 6l4 -16l-18 7l4 2l2 6l3 -4" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>
                            <div class="mb-3">
                                <div class="input-group input-group-flat">
                                    <input type="text"   dir='ltr' class="form-control" autocomplete="off" onInput={(e) => { this.handleChange({ instagram: e.target.value }) }} id='title' class="form-control" name="text-input" value={this.state.instagram} />
                                    <span class="input-group-text">
                                        <a class="link-secondary" >
                                            <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><rect x="4" y="4" width="16" height="16" rx="4" /><circle cx="12" cy="12" r="3" /><line x1="16.5" y1="7.5" x2="16.5" y2="7.501" /></svg>
                                        </a>
                                    </span>
                                </div>
                            </div>


                        </div>
                    </div>
                    <div class="row align-items-center mt-3">
                        <div class="col-4">
                            <div class="progress">
                                <div class="progress-bar" style={{ width: "75%" }} role="progressbar" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                    <span class="visually-hidden">25% Complete</span>
                                </div>
                            </div>
                        </div>
                        <div class="col">
                            <div class="btn-list justify-content-end">
                                <button onClick={() => this.submitProfile()} class="btn btn-primary">
                                    {t['continue']}
                                </button>
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            )
        }
    }


    render() {
        return (
            <div class="page page-center">
                <Validation items={this.state.validationItems} modal={this.modal} lang={this.state.lang} />
                <div class="container-tight py-4">
                    {this.checkSignUp()}

                </div>
            </div>
        )
    }
}




